import s from './App.module.scss';
import 'css/reset.scss';
import Register from '../Register/Register';
import { BrowserRouter } from 'react-router-dom';

const App = () => {
  return (
    <BrowserRouter basename="/b2bazarr">
      <div className={s['App']}>
        <Register />
      </div>
    </BrowserRouter>
  )
}

export default App
